@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}

.container {
    z-index: 1000;
    position: fixed;
    width: 100%;
    height: 50px;
    background-image: url("../../../public/images/general/header-nav/global-nav-bg.png");
    background-size: 2px;
    letter-spacing: 2px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-shadow: 1px 1px 20px rgba(255, 255, 255, .6);
}

.ham-menu {
    display: none;
    width: 32px;
}

.left-box {
    display: flex;
    flex-direction: row;
}

.logo {
    display: flex;
    align-items: center;
    transition: background-color .2s linear;
}

.logo:hover {
    background-color: #000;
}

.logo img {
    width: 100px;
    margin: 0 1rem;
}

.nav-list {
    display: flex;
    flex-direction: row;
    list-style-type: none;
    cursor: url('/public/icons/hov.png'), auto;
}

.nav-list li {
    padding: 0 1rem;
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    cursor: url('/public/icons/hov.png'), auto;
}
.nav-list li :active {
    cursor: url('/public/icons/conf.png'), auto;
}
.nav-list li a {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #969696;
    font-weight: 900;
    font-size: 13px;
    transition: color .2s linear;
    cursor: url('/public/icons/hov.png'), auto;
}

.nav-list li:hover {
    background-image: linear-gradient(to right bottom, #000000, #060404, #0b0806, #0e0c09, #0f100c);
    text-shadow: 1px 1px 10px #ed772d;
    color: #ed772d;
}

.nav-list li a:hover {
    color: #ed772d;
}
.right-box {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.log-part {
    color: #969696;
    margin-right: 1rem;
}

.log-part a {
    padding: 0 8px;
    text-decoration: none;
    color: #969696;
    font-weight: 900;
    font-size: 13px;
    transition: color .2s linear;
}

.log-part a:hover {
    color: #fff;
}

.shield-part {
    height: 50px;
    transition: background-color .2s linear,
    color .2s linear;
    padding: 0 1rem;
    display: flex;
    align-items: center;
    color: #969696;
    font-weight: 900;
    font-size: 13px;
    cursor: url('/public/icons/hov.png'), auto;
}

.shield-part:hover {
    background-color: #000;
    color: #fff;
    cursor: url('/public/icons/hov.png'), auto;
}
.shield-part a{
    cursor: url('/public/icons/hov.png'), auto;

}
.shield-part img {
    width: 20px;
    margin-right: .5rem;
    cursor: url('/public/icons/hov.png'), auto;
}

.pre-part {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    padding: 0 1rem;
    color: #fff;
    background-image: linear-gradient(6deg, #ed772d 0%, #ed772d 30% 70%, #ed772d 100%);
    font-weight: 900;
    font-size: 16px;
    text-shadow: 2px 2px 10px rgba(0, 0, 0, .8);
    cursor: url('/public/icons/hov.png'), auto;
}
.pre-part :active {
    cursor: url('/public/icons/conf.png'), auto;
}
.pre-part a :active {
    cursor: url('/public/icons/conf.png'), auto;
}
.pre-part a {
    all: unset; /* Resets all styles */
    cursor: url('/public/icons/hov.png'), auto; /* Optionally, set the cursor to pointer */
}
.shield-part a {
    all: unset; /* Resets all styles */
    cursor: url('/public/icons/hov.png'), auto; /* Optionally, set the cursor to pointer */
}
.shield-part :active {
    cursor: url('/public/icons/conf.png'), auto;
}
/***** responsive codes *****/

@media screen and (max-width: 1250px) {
    .nav-list, .log-part, .shield-part.desktop-only {
        display: none; /* Hide on mobile */
    }

    .ham-menu {
        display: block;
        margin: 0 .5rem;
    }

    /* Mobile dropdown menu */
    .nav-list.active {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 50px;
        left: 0;
        width: 100%;
        background-color: rgba(0, 0, 0, 0.9);
        z-index: 999;
    }

    .nav-list.active li {
        padding: 1rem;
        border-bottom: 1px solid #333;
    }

    .nav-list.active li a {
        color: #969696;
        text-align: center;
        font-weight: bold;
    }

    .nav-list.active li a:hover {
        color: #ed772d;
    }

    /* Styles for the mobile SRMSIGKDD link */
    .mobile-srmsigkdd {
        display: flex;
        align-items: center;
        padding: 1rem;
        color: #969696;
        font-weight: bold;
    }

    .mobile-srmsigkdd img {
        width: 20px;
        margin-right: 0.5rem;
    }

    .mobile-srmsigkdd a {
        color: #969696;
        text-decoration: none;
        transition: color 0.2s;
    }

    .mobile-srmsigkdd a:hover {
        color: #ed772d; /* Change color on hover */
    }
}

@media screen and (max-width: 470px) {
    .pre-part {
        font-size: 13px!important;
        font-weight: 800;
    }

    /* Show shield-part in mobile */
    .shield-part {
        display: flex; /* Ensure it is displayed on mobile */
    }
}
